import React, { useContext } from 'react';
import { LeagueContext, LeagueConsumer } from './index.js';
import styled from 'styled-components';

const LeagueFrame = (props) => {
	const { frameNo } = props;
	const context = useContext(LeagueContext);
	const scores = context.scoreMatrix[frameNo];
	const { targetFrame } = context;

	if (scores) {
		return <LeagueConsumer>
			{(context) => (
				<FrameWrapper>
					{renderFrames(scores, targetFrame, frameNo, context)}
				</FrameWrapper>
			)}
		</LeagueConsumer>
	}

	return <FrameWrapper></FrameWrapper>
};

const renderFrames = (scores, targetFrame, frameNo, context) => {
	let frames = [];

	Object.keys(scores).map(key => {
		let value = '';

		if (scores[key].hit > 0) {
			value = scores[key].hit
		} else if (scores[key].hit == 0 && scores[key].miss > 0) {
			value = 0
		}

		if (targetFrame.frame === frameNo && targetFrame.distance === Number(key)) {
			return frames.push(
				<SelectedFrame>
					<TriesHolder>
						{renderFrameContent(scores[key])}
					</TriesHolder>
					{value}
				</SelectedFrame>
			)
		}

		return frames.push(
			<Frame key={key}>
				<TriesHolder>
					{renderFrameContent(scores[key])}
				</TriesHolder>
				{value}
			</Frame>
		)
	})

	return frames;
}

const renderFrameContent = (score, value) => {
	let tries = [];

	for (let i = 0; i < score.hit; i++) {
		tries.push(<HitMark key={i} />)
	}

	for (let i = 0; i < score.miss; i++) {
		tries.push(<MissMark key={i} />)
	}

	return tries;
}

const handleFocus = (ev) => {
	ev.target.select();
}

const FrameWrapper = styled.div`
	width: 80px;
	padding: 4px 2px;
`

const Frame = styled.div`
	border-radius: 2px;
	height: 50px;
	width: 50px;
	display: flex;
	flex-direction: column;
	margin-bottom: 4px;
	opacity: initial;
	background: rgb(255, 255, 255);
	box-shadow: 0 0 1px 1px rgba(0,0,0,.05), 0 1px 2px -2px rgba(0,0,0,.06), 0 1px 3px 0 rgba(0,0,0,.07);
	text-align: center;
	padding-top: 16px;
	box-sizing: border-box;
	transition: box-shadow .1s linear;
	overflow: scroll;
	position: relative;

	:hover {
		box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 1px 0px, rgba(0, 0, 0, 0.05) 0px 2px 2px 0px, rgba(0, 0, 0, 0.06) 0px 3px 1px -2px, rgba(0, 0, 0, 0.1) 0px 1px 5px 0px;
	}
`

const SelectedFrame = styled(Frame)`
	outline: solid 2px #317ae2;
`

const TriesHolder = styled.div`
	position: absolute;
	right: 0;
	top: 1px;
	height: 50px;
	width: 10px;
`

const HitMark = styled.div`
	height: 4px;
	width: 4px;
	border-radius: 50%;
	border: solid 2px #317ae2;
	background: #317ae2;
	margin-bottom: 2px;
`

const MissMark = styled(HitMark)`
	background: transparent;
`


export default LeagueFrame;
