import React, { useContext } from 'react';
import { LeagueContext, LeagueConsumer } from './index.js';
import styled from 'styled-components';

const renderDistances = (firstFrame, distance) => {
	let distances = [];

	if (!firstFrame) {
		return;
	}

	Object.keys(firstFrame).map(key => {
		if (key == distance) {
			return distances.push(<CurrentFrame key={key}>{`${key}m`}</CurrentFrame>)
		}

		return distances.push(<Frame key={key}>{`${key}m`}</Frame>)
	})

	return distances;
}

const LeagueFrameTotal = (props) => {
	const context = useContext(LeagueContext);
	const { distance } = context.targetFrame;
	const { scoreMatrix } = context;
	const firstFrame = scoreMatrix[0];

	return <FrameWrapper>
				{renderDistances(firstFrame, distance)}
			</FrameWrapper>
};

const FrameWrapper = styled.div`
	width: 80px;
	padding: 4px 2px;
	margin-left: 16px;
`

const Frame = styled.div`
	border-radius: 2px;
	height: 50px;
	width: 50px;
	display: flex;
	flex-direction: column;
	margin-bottom: 4px;
	opacity: initial;
	background: rgba(0,0,0,.02);
	box-shadow: 0 0 1px 1px rgba(0,0,0,.05), 0 1px 2px -2px rgba(0,0,0,.06), 0 1px 3px 0 rgba(0,0,0,.07);
	text-align: center;
	padding-top: 16px;
	box-sizing: border-box;
	transition: box-shadow .1s linear;

	:hover {
		box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 1px 0px, rgba(0, 0, 0, 0.05) 0px 2px 2px 0px, rgba(0, 0, 0, 0.06) 0px 3px 1px -2px, rgba(0, 0, 0, 0.1) 0px 1px 5px 0px;
	}
`

const CurrentFrame = styled(Frame)`
	box-shadow: 0 0 1px 1px rgba(49, 122, 226,.05), 0 1px 2px -2px rgba(49, 122, 226, .06), 0 1px 3px 0 rgba(49, 122, 226, .07);
	background: rgba(49, 122, 226, 0.2);
`

export default LeagueFrameTotal;
